.App {
  text-align: center;
}

.infoBar {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 300px;
  background: #fbfdff;
  border: solid;
  border-radius: 25px;
  text-align: left;
}


.hljs-variable {
  color: #c82829;
}

.hljs-literal {
  color: #f5871f;
}

.hljs-string {
  color: #718c00;
}